.sw-progress-bar .sw-block-field__block{border:none}.sw-progress-bar label{display:flex}.sw-progress-bar__progress-label{margin-left:auto}.sw-progress-bar .sw-progress-bar__total{width:100%;height:8px;background-color:#e0e6eb;border-radius:4px}.sw-progress-bar .sw-progress-bar__value{transition:1s width linear;height:100%;background-color:#189eff;border-radius:4px}.sw-progress-bar .sw-progress-bar__value--no-transition{transition:0s width linear}.sw-progress-bar .sw-progress-bar__value--has-error{transition:0s width linear;background-color:#de294c}