.sw-field--controls{background:#fff;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;width:42px}.sw-field--controls.disabled{background:#f0f2f5;border-color:#d1d9e0;cursor:default !important}.sw-field--controls.disabled .sw-icon:hover{cursor:default !important;color:#b3bfcc}.sw-field--controls.error{background:#fbe5ea}.sw-field--controls .sw-icon{cursor:pointer;color:#b3bfcc}.sw-field--controls .sw-icon--toggled{color:#758ca3}.sw-field--controls .sw-icon:hover{color:#758ca3}